<template>
	<div class="participate_com">
		<div class="participate_com_bg" @click="close(0)"></div>
		<div class="participate_com_box" v-if="!betFailFlag">
			<img :src="items.commodityMainImg" alt="" class="img_foods"/>
			<p class="foods_name">{{ items.commodityName }}</p>
			<p class="foods_tips">每参与1次消耗{{ participateObj.consumptionIntegral }}积分<br>投入的数量越多，中奖概率越大哦</p>
			<div class="participate_count">
				<p class="participate_count_title">参与次数</p>
				<p class="participate_count_box">
					<i class="icon_reduce_p" @click="changeCount(0)"></i>
					<span>{{ participateCount }}</span>
					<i class="icon_add_p" @click="changeCount(1)"></i>
				</p>
			</div>
			<p class="participate_com_points">
				<span class="text1">我的积分：<i>{{ participateObj.amount }}积分</i></span>
				<span class="text2" @click="toPath('/')">获取更多积分 ></span>
			</p>
			<p class="participate_btn" @click="freeGoodsBet"></p>
		</div>
		<div class="betFail" v-else @click="close(0)"></div>
		<i class="icon_closes" @click="close(0)"></i>
	</div>
</template>

<script>
	export default{
		name:'participate-com',
		props:['items'],
		data(){
			return{
				participateCount:0,
				obj:{},
				participateObj:{},
				betFailFlag:false
			}
		},
		mounted() {
			document.getElementsByTagName('body')[0].className = 'bodyhidden';
			let _this = this;
			var jsonObj = localStorage.getItem("obj");
			_this.obj = JSON.parse(jsonObj);
			_this.freeGoodsBetInfo()
		},
		methods:{
			changeCount(i){
				let _this = this;
				let ss = _this.participateObj.amount;
				
				if(!!i){
					if(ss < _this.participateObj.consumptionIntegral){
						console.log(ss);
						return false;
					}
					_this.participateCount ++;
					ss = ss - _this.participateObj.consumptionIntegral;
				}else{
					if(_this.participateCount == 0){
						_this.participateCount = 0;
					}else{
						_this.participateCount --;
						ss = ss + _this.participateObj.consumptionIntegral;
					}
				}
				_this.participateObj.amount = ss;
				
			},
			toPath(path){
				let _this = this;
				_this.$router.push({path:path})
			},
			freeGoodsBetInfo(){
				let _this = this;
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
					method: 'post',
					url: '/free/goods/betInfo',
					data:redata,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						_this.participateObj = reData;
						if(_this.participateObj.amount < _this.participateObj.consumptionIntegral){
							_this.participateCount = 0;
						}
					}
				})
			},
			freeGoodsBet(){
				let _this = this;
				var exclusiveObj = {
					activityInfoId:_this.items.activityInfoId,
					goodsId:_this.items.goodsId,
					cycle:_this.items.cycle,
					frequency:_this.participateCount
				};
				console.log(exclusiveObj);
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/free/goods/bet',
					data:redata,
				}).then(function(res){
					console.log(res);
					if(res.data.code === 200){
						_this.$toast('参与成功～');
						_this.close(_this.participateCount);
					}else if(res.data.code === 400){
						_this.betFailFlag = true;
					}
				})
			},
			close(c){
				this.$emit("clousePopup",{count:c});
			}
		},
		beforeDestroy() {
			document.getElementsByTagName('body')[0].className = 'bodyshow';
		}
	}
</script>

<style scoped>
	.participate_com{
		width: 100%;
		min-height: 100vh;
		height: 100%;
		position: fixed;
		top:0;
		left: 0;
		background-color: rgba(0,0,0,0.7);
		z-index: 9999;
	}
	.participate_com_bg{
		width: 100%;
		height: 100%;
		position: absolute;
		top:0;
		left: 0;
		z-index: 10000;
	}
	.participate_com_box{
		width: 8rem;
		background-color: #fff;
		box-sizing: border-box;
		border:0.16rem solid #fcebfe;
		border-radius: 0.5rem;
		padding:0.72rem 0.45rem 0.4rem 0.45rem;
		position: absolute;
		top:50%;
		left:50%;
		transform: translateX(-50%) translateY(-50%);
		z-index: 10001;
	}
	.img_foods{
		width: 2.4rem;
		height: 2.4rem;
		margin:0 auto;
	}
	.foods_name{
		width: 100%;
		font-size: 0.36rem;
		font-weight: 600;
		color:#000;
		text-align: center;
		line-height: 1rem;
		border-bottom:0.02rem solid #d2d2d2;
	}
	.foods_tips{
		width: 100%;
		text-align: center;
		line-height: 0.6rem;
		margin-top:0.3rem;
		color:#848383;
	}
	.participate_count{
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 6.36rem;
		height: 1.04rem;
		background: linear-gradient(to top,#fb4377,#ff73b5);
		border-radius: 0.52rem;
		box-sizing: border-box;
		padding:0 0.2rem;
	}
	.participate_count_title{
		width: 2.5rem;
		font-size: 0.4rem;
		color:#fff;
		line-height: 1.04rem;
		margin-left:0.3rem;
	}
	.participate_count_box{
		width: 3rem;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.participate_count_box span{
		display: inline-block;
		width: 1.16rem;
		height: 0.72rem;
		color: #ff3366;
		font-size: 0.48rem;
		border-radius: 0.15rem;
		background-color: #fff;
		line-height: 0.72rem;
		text-align: center;
		margin: 0 0.12rem;
	}
	.icon_reduce_p{
		display: inline-block;
		width: 0.76rem;
		height: 0.79rem;
		background-image: url(../static/img/icon_reduce_p.png);
		background-repeat: no-repeat;
		background-size: 100%;
	}
	.icon_add_p{
		display: inline-block;
		width: 0.76rem;
		height: 0.79rem;
		background-image: url(../static/img/icon_add_p.png);
		background-repeat: no-repeat;
		background-size: 100%;
	}
	.participate_com_points{
		display: flex;
		font-size: 0.3rem;
		justify-content: space-between;
		margin-top:0.7rem;
	}
	.participate_com_points .text1{
		color: #000;
	}
	.participate_com_points .text1 i{
		color:#ff3366;
	}
	.participate_com_points .text2{
		color:#0078ff;
	}
	.participate_btn{
		width: 6.36rem;
		height: 1.32rem;
		background: url(../static/img/btn_participate.png) no-repeat;
		background-size: 100%;
		margin:0.3rem auto 0 auto;
	}
	.betFail{
		width: 8rem;
		height: 8.04rem;
		background: url(../static/img/icon_betfail.png) no-repeat;
		background-size: 100%;
		position: absolute;
		top:50%;
		left:50%;
		transform: translateX(-50%) translateY(-50%);
	}
	.icon_closes{
		  display: block;
		  width: 1rem;
		  height: 1rem;
		  margin:0.5rem auto;
		  background-image: url(../static/img/icon_close.png);
		  background-repeat: no-repeat;
		  background-size: 100%;
		  position: absolute;
		  bottom: 2rem;
		  left:50%;
		  transform: translateX(-50%);
	}
</style>
