<template>
	<div class="foodsDetails">
		<div class="foodsDetails_header">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p class="foodsDetails_title">商品详情</p>
		</div>
		<div class="swiper">
			<van-swipe @change="onChange">
			  <van-swipe-item v-for="(item,index) in detailsObj.commodityMainImg" :key="index">
				  <img :src="item" class="img_swiper">
			  </van-swipe-item>
			  <template #indicator>
			    <div class="custom-indicator">{{ current + 1 }}/{{ detailsObj.commodityMainImg.length }}</div>
			  </template>
			</van-swipe>
			<div class="touzhu">
				<div class="main" v-for="(text,index) in participantList" :key="index">
					<div class="bg"></div>
					<p>{{ text }}</p>
				</div>
			</div>
		</div>
		<div class="foodsDetails_price">
			<div class="bg1"></div>
			<div class="bg2"></div>
			<div class="bg3"></div>
			<div class="icon_equal"></div>
			<div class="icon_reduce"></div>
			<span class="price1">￥{{ detailsObj.marketPrice }}</span>
			<span class="price2">￥{{ detailsObj.officialPrice }}</span>
			<span class="price3">￥{{ detailsObj.platformSubsidy }}</span>
			<span class="text1">{{ supplier[detailsObj.supplier] }}售价</span>
			<span class="text2">平台补贴</span>
			<p class="btn_foodsDetails_price" @click="toDeepLink(detailsObj.goodsUrl)">前往购买</p>
			<img src="../../static/img/img_ykj.png" alt="" class="img_ykj" v-if="openPrizeFlag"/>
			<div class="price_countDown" v-else>
				<p class="price_countDown_text">
					<i class="icon_time_bai"></i>
					<span>距离开奖还剩</span>
				</p>
				<van-count-down :time="time" @finish="finish">
				  <template #default="timeData">
					<span class="day" v-if="timeData.days != 0">{{ timeData.days }}天</span>
				    <span class="block">{{ timeData.hours > 9 ? timeData.hours : '0'+timeData.hours }}</span>
				    <span class="colon">:</span>
				    <span class="block">{{ timeData.minutes > 9 ? timeData.minutes : '0'+timeData.minutes }}</span>
				    <span class="colon">:</span>
				    <span class="block">{{ timeData.seconds > 9 ? timeData.seconds : '0'+timeData.seconds }}</span>
				  </template>
				</van-count-down>
			</div>
		</div>
		<div class="foodsDetails_info">
			<div class="foodsDetails_info_box">
				<div class="foodsDetails_box_title">
					<p>
						<img :src="detailsObj.logo" alt="" class="icon_logo">
						<span class="commodityName">{{ detailsObj.commodityName }}</span>
					</p>
					<p class="userCycleInvestmentCount">您已参与 <span>{{ detailsObj.userCycleInvestmentCount }}</span> 次</p>
				</div>
				<p class="cycle_quantity">
					<i class="icon_flag_bai"></i>
					<span>第{{ item.cycle }}期</span>
					<i class="icon_gift_bai"></i>
					<span>共{{ item.quantity }}份免费奖品</span>
				</p>
				<p class="totalCount_endTime">
					<i class="icon_heat"></i>
					<span>已参与{{ detailsObj.totalCount }}次</span>
					<i class="icon_time"></i>
					<span>开奖时间：{{ item.endTimeStr }}</span>
				</p>
				<p class="number_publicity">
					<span @click="toPath('/freeAccess/participateNumber')">我的参与编号 ></span>
					<span @click="toPath('/freeAccess/winningPublicity')">往期中奖公示 ></span>
				</p>
				<p class="process">
					<span>投入积分参与</span>
					<i></i>
					<span>等待开奖</span>
					<i></i>
					<span>抽中免费领取奖品</span>
					<span class="process_btn" @click="toPath('/activityRules')">活动规则</span>
				</p>
			</div>
		</div>
		<div class="sponsor" @click="toDeepLink(detailsObj.storeAddress)">
			<img :src="detailsObj.storeLogo" alt="" class="img_sponsor_header">
			<div class="sponsor_box">
				<p class="text1">本宝贝由 <span>{{ detailsObj.storeName }}</span> 赞助</p>
				<p class="text2">更多优惠活动等着你</p>
			</div>
			<i class="icon_arrow_right_weight"></i>
		</div>
		<div class="foodsDetails_img">
			<p><span>商品详情</span></p>
			<div v-for="(item,index) in detailsObj.commodityInfoImg" :key="index">
				<img :src="item" />
			</div>
		</div>
		<div class="foodsDetails_btn">
			<p class="foodsDetails_text" v-if="openPrizeFlag">直接购买也很划算哦 ></p>
			<div class="fml_btn" @click="bet" v-else>免费领</div>
			<div :class="detailsObj.supplier == 'TAOBAO'?'thgm2 thgm_tb':'thgm2 thgm_jd'" v-if="openPrizeFlag" @click="toDeepLink(detailsObj.goodsUrl)">
				<img :src="detailsObj.angleMark" alt="" class="img_thgm"/>
				<span class="text1">特惠购买￥</span>
				<span class="text2">{{ detailsObj.marketPrice }}</span>
			</div>
			<div :class="detailsObj.supplier == 'TAOBAO'?'thgm1 thgm_tb':'thgm1 thgm_jd'" v-else @click="toDeepLink(detailsObj.goodsUrl)">
				<img :src="detailsObj.angleMark" alt="" class="img_thgm"/>
				<span class="text1">特惠购买￥</span>
				<span class="text2">{{ detailsObj.marketPrice }}</span>
			</div>
		</div>
		<div class="popup" v-if="flagPopup">
			<div class="popup_main">
				<i class="icon_close" @click="closePopup"></i>
				<p class="popup_main_top">
					<i class="icon_yy" v-if="isWin"></i>
					<i class="icon_sx" v-if="!isWin"></i>
					<span v-if="isWin">恭喜您，中奖啦！</span>
					<span v-if="!isWin">很遗憾本期未中奖，请再接再厉！</span>
					<img src="../../static/img/img_top_zs.png" class="img_top_zs" v-if="isWin"/>
				</p>
				<div class="popup_main_goods">
					<div class="popup_main_goods_fs">
						<img :src="item.commodityMainImg" alt="" class="img_goods">
						<div class="goods_list_box">
							<p class="text1">
								<img :src="item.logo" alt="" class="icon_logo">
								<span>{{ item.commodityName }}</span>
							</p>
							<p class="text2">
								<i class="icon_time"></i>
								<span>开奖时间：{{ item.endTimeStr }}</span>
							</p>
							<p class="text3">
								<i class="icon_gift"></i>
								<span>共{{ item.quantity }}份奖品</span>
								<i class="icon_heat"></i>
								<span>已参与{{ detailsObj.totalCount }}次</span>
							</p>
						</div>
					</div>
					<div class="win_box" v-if="isWin && !isReceive">
						<p class="win_box_text1">请在{{ winPrizeObj.expirationTime }}前领取奖品，过期将自动作废！</p>
						<div class="win_box_box">
							<p class="text1">您的中奖编号</p>
							<p class="text2">{{ winPrizeObj.investmentNo }}</p>
							<p class="btn" @click="toReceive"></p>
						</div>
					</div>
					<div class="win_box" v-if="isWin && isReceive">
						<p class="win_box_text1">奖励已被领取过了哦，赶快去参加其他商品吧 </p>
						<div class="win_box_box">
							<p class="text1" v-if="winPrizeObj.expressNum">物流单号：{{ winPrizeObj.expressName+' '+winPrizeObj.expressNum }}</p>
							<p class="text1" v-else>正在发货中···</p>
						</div>
					</div>
					<van-swipe :show-indicators="false" v-if="!isWin">
						<van-swipe-item>
							<div class="van_swipe" v-for="(item,index) in winUserList" :key="index">
								<div class="van_swipe_text1">
									<p class="text1">中奖用户：{{ item.userName || '匿名用户' }}</p>
									<p class="text2">参与次数：{{ item.participationNum }}次</p>
								</div>
								<div class="van_swipe_text2">
									<p class="text1">中奖编号</p>
									<p class="text2">{{ item.investmentNo }}</p>
								</div>
							</div>
						</van-swipe-item>
					</van-swipe>
					<p class="number_publicity dis_flex">
						<span @click="toPath('/freeAccess/participateNumber')">我的参与编号 ></span>
						<span @click="toPath('/freeAccess/winningPublicity')">往期中奖公示 ></span>
					</p>
					<p class="goods_list_stage">第{{ item.cycle }}期</p>
				</div>
				<div class="hot_goods">
					<img src="../../static/img/img_hot_goods_title.png" class="hot_goods_title"/>
					<ul class="goods_list">
						<li v-for="(items,index) in foodsList" :key="index">
							<img :src="items.commodityMainImg" alt="" class="img_goods">
							<div class="goods_list_box">
								<p class="text1">
									<img :src="items.logo" alt="" class="icon_logo">
									<span>{{ items.commodityName }}</span>
								</p>
								<p class="text2">
									<i class="icon_time"></i>
									<span>开奖时间：{{ items.endTime }}</span>
								</p>
								<p class="text3">
									<i class="icon_gift"></i>
									<span>共{{ items.quantity }}份奖品</span>
									<i class="icon_heat"></i>
									<span>已参与{{ detailsObj.totalCount }}次</span>
								</p>
								<p class="text4">
									<span class="span1">0元 | 包邮</span>
									<span class="span2">{{ '￥ '+items.officialPrice }}</span>
								</p>
							</div>
							<p class="goods_list_stage">第{{ items.cycle }}期</p>
							<p class="goods_list_btn"></p>
						</li>
					</ul>
				</div>
			</div>
		</div>
		<div class="kjl_popup" v-if="kjlFlag">
			<div class="kjl_popup_main" v-if="kjlRequestFlag">
				<img :src="item.commodityMainImg" class="img_kjl_popup_main"/>
				<p class="kjl_popup_main_title">{{ item.commodityName }}</p>
				<div class="btn_kjl_popup_main" @click="prizeOpenPrize"></div>
				<i class="icon_closes" @click="kjlFlag = false"></i>
			</div>
			<div class="betFail" v-else @click="prizeOpenPrize">
				<i class="icon_closes" @click="kjlFlag = false"></i>
			</div>
		</div>
		<participate-com v-if="betFlag" :items="item" @clousePopup="clousePopup"></participate-com>
	</div>
</template>

<script>
	import participateCom from '../../components/participate-com.vue'
	import anime from '../../assets/js/anime.min.js';
	export default{
		name:'foodsDetails',
		components:{
			participateCom
		},
		data(){
			return{
				kjlFlag:false,
				kjlRequestFlag:true,
				finishFlag:false,
				time:0,
				isReceive:false,
				isWin:false,
				flagPopup:false,
				openPrizeFlag:false,
				betFlag:false,
				supplier:{
					TAOBAO:'淘宝',
					JD:'京东'
				},
				obj:{},
				item:{},
				current: 0,
				detailsObj:{
					commodityMainImg:[],
					commodityInfoImg:[]
				},
				foodsList:[],
				winUserList:[],
				winPrizeObj:{},
				participantList:['乐友075刚刚参与了2次']
				
			}
		},
		mounted() {
			let _this = this;
			var timestamp = Date.parse(new Date());
			var jsonObj = localStorage.getItem("obj");
			var itemObj = localStorage.getItem("itemObj");
			_this.obj = JSON.parse(jsonObj);
			_this.item = JSON.parse(itemObj);
			_this.time = _this.item.endTime-timestamp;
			_this.freeGoodsInfo();
			_this.prizeOpenPrize();
			_this.freeGoodsParticipant();
		},
		methods:{
			freeGoodsParticipant(){
				let _this = this;
				_this.adLoadingFlag = true;
				var exclusiveObj = {
					activityInfoId:_this.item.activityInfoId,
					goodsId:_this.item.goodsId,
					cycle:_this.item.cycle
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				console.log(requestData);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/free/goods/participant',
					data:redata,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log('participant======',reData);
						_this.participantList = reData;
						anime({
							targets: '.touzhu .main',
							opacity:[
								{ value: 1, duration: 200},
								{ value: 0, duration: 1500},
							],
							translateY:[
								{ value: 0, duration: 200},
								{ value: '-200%', duration: 1500},
							],
							easing: 'linear',
							loop: true,
							duration: 1700,
							delay: anime.stagger(1000) // 每个元素的延迟增加100毫秒。
						});
					}
					_this.adLoadingFlag = false;
				})
			},
			toDeepLink(url){
				window.location.href = url;
			},
			onChange(index){
				this.current = index;
			},
			goBack(){
				this.$router.go(-1);
			},
			toPath(path){
				let _this = this;
				let query = {};
				if(path.indexOf('participateNumber') != -1 || path.indexOf('winningPublicity') != -1){
					query.item = _this.item;
				}
				_this.$router.push({path:path,query:query})
			},
			finish() {
				if(this.time<0){
					console.log('倒计时还未开始就结束了！！！');
				}else{
					console.log('倒计时结束了！！！');
					this.kjlFlag = true;
				}
				console.log(this.time)
			  this.finishFlag = true;
			},
			toSponsor(url){
				console.log(url);
			},
			freeGoodsInfo(){
				let _this = this;
				_this.adLoadingFlag = true;
				var exclusiveObj = {
					activityInfoId:_this.item.activityInfoId,
					goodsId:_this.item.goodsId,
					cycle:_this.item.cycle
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/free/goods/info',
					data:redata,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						_this.detailsObj = reData;
					}
					_this.adLoadingFlag = false;
				})
			},
			prizeOpenPrize(){
				let _this = this;
				_this.adLoadingFlag = true;
				var exclusiveObj = {
					activityInfoId:_this.item.activityInfoId,
					goodsId:_this.item.goodsId,
					cycle:_this.item.cycle
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/prize/openPrize',
					data:redata,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log(reData);
						if(reData.status){
							_this.kjlFlag = false;
							_this.flagPopup = true;
							_this.openPrizeFlag = true;
							_this.prizeWinPrize(reData.winPrizeId);
							_this.winningUserList(reData.winPrizeId);
						}else{
							if(_this.finishFlag){//倒计时结束 表示正在开奖
								_this.kjlRequestFlag = false;
							}
						}
					}
					_this.adLoadingFlag = false;
				})
			},
			prizeWinPrize(winPrizeId){
				let _this = this;
				_this.adLoadingFlag = true;
				var exclusiveObj = {
					activityInfoId:_this.item.activityInfoId,
					goodsId:_this.item.goodsId,
					cycle:_this.item.cycle,
					winPrizeId:winPrizeId
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/prize/winPrize',
					data:redata,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log(reData);
						_this.isWin = reData.isWinPrize;
						_this.isReceive = reData.receiveStatus;
						reData.expirationTime = _this.$timestampTpDate(reData.expirationTime,'ymd');
						_this.winPrizeObj = reData;
					}
					_this.freeGoodsList('populargoods');
					_this.adLoadingFlag = false;
				})
			},
			bet(){
				this.betFlag = true;
			},
			clousePopup(data){
				// this.freeGoodsInfo();
				this.detailsObj.userCycleInvestmentCount += data.count;
				this.detailsObj.totalCount += data.count;
				this.betFlag = false;
			},
			closePopup(){
				this.flagPopup = false; 
			},
			freeGoodsList(type){
				let _this = this;
				_this.adLoadingFlag = true;
				var exclusiveObj = {
					commodityType:type
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/free/goods/list',
					data:redata,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						for(var i in reData){
							reData[i].endTime = _this.$timestampTpDate(reData[i].endTime,'ymdhms');
						}
						_this.foodsList = reData;
					}
					_this.adLoadingFlag = false;
				})
			},
			winningUserList(winPrizeId){
				let _this = this;
				_this.adLoadingFlag = true;
				var exclusiveObj = {
					winPrizeId:winPrizeId
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/prize/winningUserList',
					data:redata,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						_this.winUserList = reData;
					}
					_this.adLoadingFlag = false;
				})
			},
			toReceive(){
				let _this = this;
				var receiveObj = JSON.stringify(_this.item);
				localStorage.setItem("receiveObj",receiveObj);
				_this.$router.push({path:'/freeAccess/receive'});
			}
		}
	}
</script>

<style scoped>
	.foodsDetails{
		width: 10.8rem;
		min-height: 100vh;
		background-color: #fcebfe;
		padding-bottom:1.62rem;
	}
	.foodsDetails_header{
		display: flex;
		align-items:center;
		width: 10.8rem;
		height: 2.32rem;
		box-sizing: border-box;
		padding-top:var(--barHeight);
		padding-left:0.3rem;
		padding-right:0.3rem;
		position: relative;
		background: linear-gradient(to left,#fe7773,#fb4377,#fa8dea);
	}
	.icon_arrow_left{
	  display: inline-block;
	  width: 0.56rem;
	  height:0.56rem;
	  background-image: url(../../static/img/icon_arrow_left.png);
	  background-repeat: no-repeat;
	  background-size: 100%;
	  z-index: 4;
	}
	.foodsDetails_title{
		width: 100%;
		height: 1.52rem;
		line-height: 1.52rem;
		position: absolute;
		left:0;
		bottom:0;
		font-size: 0.48rem;
		color:#fff;
		text-align: center;
		z-index: 3;
	}
	.swiper{
		position: relative;
	}
	.img_swiper{
		width: 10.8rem;
		height: 10.06rem;
	}
	.custom-indicator{
		display: inline-block;
		color:#fff;
		background-color: rgba(0,0,0,0.5);
		border-radius: 0.36rem;
		line-height: 0.72rem;
		padding:0 0.2rem;
		font-size: 0.36rem;
		position: absolute;
		bottom:0.27rem;
		right: 0.25rem;
	}
	.foodsDetails_price{
		width: 10.8rem;
		height: 2.74rem;
		background: linear-gradient(to left,#fe7773,#fb4377,#fa8dea);
		position: relative;
	}
	.foodsDetails_price .bg1{
		width: 3.58rem;
		height: 0.63rem;
		background-image: url(../../static/img/bg_foodsDetails_price1.png);
		background-size: 100%;
		background-repeat: no-repeat;
		position: absolute;
		top:0.55rem;
		left:4.32rem;
	}
	.foodsDetails_price .bg2{
		width: 10.3rem;
		height:1.84rem;
		background-image: url(../../static/img/bg_foodsDetails_price2.png);
		background-size: 100%;
		background-repeat: no-repeat;
		position: absolute;
		top:0.6rem;
		left:0.23rem;
	}
	.foodsDetails_price .bg3{
		width: 2.08rem;
